import * as React from "react";
import { Box, makeStyles, Theme, createStyles } from "@material-ui/core";
import { ToggleButtonGroup, ToggleButton } from "@material-ui/lab";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      backgroundColor: theme.palette.background.paper,
      display: "flex",
      padding: theme.spacing(2, 0)
    }
  })
);

export default function Sort() {
  const classes = useStyles();
  const [sorts, setSorts] = React.useState(() => [1, 2]);

  const _changeCall = (_, newSorts: any[]) => {
    setSorts(newSorts);
  };

  return (
    <Box className={classes.root}>
      <ToggleButtonGroup value={sorts} onChange={_changeCall}>
        <ToggleButton value={1}></ToggleButton>
        <ToggleButton value={2}></ToggleButton>
        <ToggleButton value={3}></ToggleButton>
        <ToggleButton value={4}></ToggleButton>
      </ToggleButtonGroup>
    </Box>
  );
}
